import { Component } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, FormControl, ValidatorFn } from '@angular/forms';
import { Http, Response } from '@angular/http';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent {
  link = 'http://api.giphy.com/v1/gifs/random?api_key=dc6zaTOxFJmzC&tag=';
  tag = 'fail';
  http: Http;
  form: FormGroup;
  orders = [
    { id: 1, name: 'answer 1' },
    { id: 2, name: 'answer 2' },
    { id: 3, name: 'answer 3' },
    { id: 4, name: 'answer 4' }
  ];
  // random = 'http://api.giphy.com/v1/gifs/random?api_key=dc6zaTOxFJmzC&q=';
  // tag = 'fail';
  // http: Http;
  // giphy = [];

  constructor(private formBuilder: FormBuilder,
    http: Http) {
    this.http = http;
    const controls = this.orders.map(c => new FormControl(false));
    // controls[0].setValue(true);

    this.form = this.formBuilder.group({
      orders: new FormArray(controls, minSelectedCheckboxes(1))
    });
  }

  submit() {
    const selectedOrderIds = this.form.value.orders
      .map((v, i) => v ? this.orders[i].id : null)
      .filter(v => v !== null);

    console.log(selectedOrderIds);
  }

randomGif(searchTerm: HTMLInputElement): void {
  const apiLink = this.link + this.tag;

  this.http.request(apiLink)
      .subscribe((res: Response) => {
            this.link = res.json().data;
            console.log(this.link);
      });
}

}

function minSelectedCheckboxes(min = 1) {
  const validator: ValidatorFn = (formArray: FormArray) => {
    const totalSelected = formArray.controls
      .map(control => control.value)
      .reduce((prev, next) => next ? prev + next : prev, 0);

    return totalSelected >= min ? null : { required: true };
  };

  return validator;
}


